@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.footer {
    width: 100%;
    height: 100px;
    background-color: rgb(22, 22, 22);
    color: whitesmoke;
}
.footer-container {
    width: 100%;
    height: 100%;
    display: flex;
    font-size: 17px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    justify-content: center;
    align-items: center;
}
.header {
    width: 100%;
    height: 80vh;
}

.header-container {
    width: 100%;
    height: 85vh;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.header-box-header {
    width: 50%;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
}

.line-title {
    overflow: hidden;
}
.slideDown{
    display: inline-block;
    transform: translateY(80%);
    font-size: 80px;
    color: whitesmoke;
    font-family: 'Fredericka the Great', cursive;
}

.header-local {
    display: inline-block;
    transform: translateY(100%);
    margin-top: 20px;
    font-size: 30px;
    color: whitesmoke;
    font-weight: lighter;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.header-show-more-item {
    position: absolute;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0px;
    overflow: hidden;
    color: whitesmoke;
    margin-bottom: 20px;
}
.showmore {
    transform: translateY(100%);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 25px;
}

.icon-droparrow {
    transform: translateY(100%);
    display: flex;
    align-items: center;
    font-size: 30px;
    margin-left: 10px;
}

@media screen and (max-width: 450px) {

    .header-box-header {
        width: 100%;
    }

    .slideDown {
        font-size: 35px;
    }
    .header-local {
        font-size: 15px;
    }
    .showmore {
        font-size: 20px;
    }
    .icon-droparrow {
        font-size: 20px;
    }

}
.infotext {
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    background-color: black;
    color: whitesmoke;
}

.info-container {
    padding-top: 10%;
    padding-bottom: 20%;
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
}

.text-scroll {
    font-size: 100px;
    display: inline-block;
    transform: translateX(100%);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    right: 0;
}
.text-scroll-1 {
    font-size: 100px;
    display: inline-block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    transform: translateX(-100%);
}

@media screen and (max-width: 768px) {
    .text-scroll {
        font-size: 50px;
    }
    .text-scroll-1 {
        font-size: 50px;
    }
}
@media screen and (max-width: 600px) {
    .text-scroll {
        font-size: 30px;
        margin-bottom: 30px;
    }
    .text-scroll-1 {
        font-size: 30px;
    }
}
@media screen and (max-width: 450px) {
    .text-scroll {
        font-size: 25px;
        padding: 10px;
        margin-bottom: 30px;
    }
    .text-scroll-1 {
        padding: 10px;
        font-size: 25px;
    }
}
#showisland {
    width: 100%;
    height: 300vh;
    overflow: hidden;
    border: none;
    background-color: black;
}
.showisland-title-next {
    height: 100vh;
    background-color: black;
}

.show-container {
    width: 100%;
    height: 100vh;
}

.showisland-box-now {
    display: flex;
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 900px ) {
    .showisland-box-now {
        flex-direction: column;
    }
    .show-effect-text {
        text-align: center;
        font-size: 25px;
    }
    .show-effect-text-1 {
        text-align: center;
        font-size: 25px;
    }
    .show-effect-text-2 {
        font-size: 25px;
        text-align: center;
    }
    .picture-slide-1 {
        order: 1;
    }
    .show-text-title-1 {
        order: 2;
    }
}
.show-picture {
    flex: 50% 1;
    overflow: hidden;
    height: 100%;
}
.picture-slide {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all 2s ease-in-out;
    background-position: center;
    transform: translateX(-90%);
    cursor: pointer;
    background-image: url(/pahawang-island/static/media/gambar1.a32ea5cf.jpg);
    
}
.picture-slide-1 {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;transform: translateX(90%);
    transition: all 2s ease-in-out;
    cursor: pointer;
    background-image: url(/pahawang-island/static/media/gambar2.b959b673.jpg);
    
}
.picture-slide-2 {
    width: 100%;
    height: 100%;
    background-size: cover;
    transform: translateX(-90%);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    transition: all 2s ease-in-out;
    background-image: url(/pahawang-island/static/media/gambar3.2e170cec.jpeg);
    
}
:hover.picture-slide-1,:hover.picture-slide-2 {
    transform: scale(1.2);
    background-color: rgba(77, 77, 77, 0.103);
}

.shadow-picture {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.397);
}

.show-text-title {
    flex: 50% 1;
    border: none;
    display: flex;
    color: whitesmoke;
    font-size: 40px;
    padding: 20px;
    overflow: hidden;
    text-align: start;
    justify-content: center;
    align-items: center;
}
.show-effect-text{
    display: inline-block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    transform: translateX(-100%);

}
.show-text-title-1 {
    flex: 50% 1;
    border: none;
    display: flex;
    color: whitesmoke;
    font-size: 40px;
    padding: 20px;
    text-align: end;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}
.show-effect-text-1{
    display: inline-block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    transform: translateX(100%);

}
.show-text-title-2 {
    flex: 50% 1;
    border: none;
    display: flex;
    color: whitesmoke;
    font-size: 40px;
    padding: 20px;
    overflow: hidden;
    text-align: start;
    justify-content: center;
    align-items: center;
}
.show-effect-text-2{
    display: inline-block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    transform: translateX(-100%);

}
.topbar {
    width: 100%;
    height: 100px;
    position: absolute;
    z-index: 9999;
}
.topbar::after{
    display: block;
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         right: 100%;
         width: 0%;
         height: 100%;
         background-color: rgb(197, 67, 67);
}
.topbar-container{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
}
.topbar-navbar {
    width: 100%;
    height: 100%;
    display: flex;
    max-width: 1600px;
    padding: 0 10px;
    align-items: center;
    justify-content: space-between;
}
.navbar-logo {
    font-size: 30px;
    color: whitesmoke;
    margin-left: 30px;
    cursor: pointer;
    font-family: 'Fredericka the Great', cursive;
}
.navbar-logo:hover{
    transform: rotate(45%);
}
.navbar-menu {
    font-size: 30px;
    color: whitesmoke;
    opacity: 0;
    cursor: pointer;
    margin-right: 30px;
    font-family: 'Fredericka the Great', cursive;
}

@media screen and (max-width: 450px ) {
    .navbar-logo {
        margin-left: 10px;
        font-size: 20px;
    }
    .navbar-menu {
        font-size: 20px;
        margin-right: 10px;
    }
}

.sidebar {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    height: 100vh;
    display: flex;
    z-index: 2;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 80px;
    background-color: black;
    color: white;
}

.back {
    cursor: pointer;
}
.menu-navigation {
    display: none;
    position: relative;
    top: 100px;
    left: 0;
    right: 0;
    z-index: 9;
    color: whitesmoke;
    position: fixed;
    height: 100vh;
    width: 100%;
    overflow: hidden;
}
.menu-secondary-background-color{
    background: rgba(103, 91, 59, 0.555);
    background: radial-gradient(circle, rgba(103,91,59,1) 0%, rgba(0,0,0,1) 100%);
    top: 100px;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
    height: 100%;
    background-repeat: no-repeat;
    width: 100%;
    z-index: -2;
    overflow: hidden;
}
.menu-layer{
    position: relative;
    background: url(/pahawang-island/static/media/pexels-jeremy-bishop-2397651.beef1d06.jpg);
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
    border-top: 10px solid rgba(255, 255, 255, 0.76);
    overflow: hidden;
}
.menu-layer-bg {
    width: 100%;
    height: 100%;
    background-color: rgba(17, 49, 78, 0.671);
}
.menu-link{
    width: 100%;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
ul{
    padding:50px 10px;
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    overflow: hidden;
}

li{
    list-style: none;
    font-size: 5rem;
    font-weight: 100;
    cursor: pointer;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    padding: 10px;
    transform: translateY(-10%);
}
li:hover {
    color: rgb(199, 51, 51);
}


.container-home {
    height: 100vh;
    width: 100%;
    background: url(/pahawang-island/static/media/fidelia-zheng-SRd8je3VIcE-unsplash.e6f2835a.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.shadow-home-img {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.445);
}

#my-scrollbar {
    height: 100vh;
    overflow: hidden;


  }
